import React, { FC } from 'react';
import { useForm } from 'react-hook-form';
import { Card, CardBody, CardHeader, Col, CustomInput, Row } from 'reactstrap';
import { DetailEditForm, DetailEditViewFormProps, InputGroupInline, InputType } from 'summer';
import { TomadorServicoTelefone } from '../../../../models/dtos/mfp/tomadorServicoTelefone';

const TomadorServicoTelefoneEditView: FC<DetailEditViewFormProps<TomadorServicoTelefone>> = props => {
  const formProps = useForm<TomadorServicoTelefone>({ defaultValues: props.selectedItem || {}, mode: 'onBlur' });

  return (
    <DetailEditForm handleSubmit={props.handleSubmit} handleCancel={props.handleCancel} formProps={formProps}>
      <Row>
        <Col>
          <InputGroupInline formProps={formProps} id="contato" isPrependLabel={true} label="Contato" />
        </Col>
      </Row>
      <Row>
        <Col>
          <InputGroupInline
            formProps={{ ...formProps, validation: { required: true } }}
            id="numero"
            isPrependLabel={true}
            label="Telefone"
            type={InputType.TELEFONE}
          />
        </Col>
      </Row>
      <Row>
        <Col style={{ marginBottom: '30px' }}>
          <Card>
            <CardHeader className="font-weight-bold bg-light">Tipo</CardHeader>
            <CardBody>
              <Row style={{ marginBottom: '10px' }}>
                <Col>
                  <CustomInput
                    type="switch"
                    label="Principal"
                    name="tipoTelefonePrincipal"
                    id="checktipoTelefonePrincipal"
                    innerRef={formProps.register}
                  />
                </Col>
                <Col>
                  <CustomInput
                    type="switch"
                    label="Residencial"
                    name="tipoTelefoneResidencial"
                    id="checktipoTelefoneResidencial"
                    innerRef={formProps.register}
                  />
                </Col>
              </Row>
              <Row style={{ marginBottom: '10px' }}>
                <Col>
                  <CustomInput
                    type="switch"
                    label="Comercial"
                    name="tipoTelefoneComercial"
                    id="checktipoTelefoneComercial"
                    innerRef={formProps.register}
                  />
                </Col>
                <Col>
                  <CustomInput type="switch" label="Celular" name="tipoTelefoneCelular" id="checktipoTelefoneCelular" innerRef={formProps.register} />
                </Col>
              </Row>
              <Row>
                <Col>
                  <CustomInput type="switch" label="Fax" name="tipoTelefoneFax" id="checktipoTelefoneFax" innerRef={formProps.register} />
                </Col>
              </Row>
            </CardBody>
          </Card>
        </Col>
      </Row>
    </DetailEditForm>
  );
};

export default TomadorServicoTelefoneEditView;
