import React, { FC } from 'react';
import { enumColumn, InputGroupInline, InputProps, InputType } from 'summer';
import { CLASS_NAME } from '../../../../models/dtos/mfp/lotacaoTributaria';
import { TIPO_LOTACAO_TRIBUTARIA_ENUM_VALUES } from '../../../../models/enumerated/mfp/tipoLotacaoTributariaEnum';

const LotacaoTributariaIS: FC<InputProps> = props => {
  return (
    <InputGroupInline
      {...props}
      type={InputType.INPUT_SEARCH}
      inputSearchProps={{
        ...props.inputSearchProps,
        columns: [
          enumColumn(
            {
              dataField: 'tipoLotacaoTributaria',
              text: 'Tipo de Lotação',
            },
            TIPO_LOTACAO_TRIBUTARIA_ENUM_VALUES
          ),
          {
            dataField: 'descricao',
            text: 'Descrição',
          },
        ],
        dtoClassName: CLASS_NAME,
        searchViewId: 'LotacaoTributariaSearchView',
      }}
    />
  );
};

export default LotacaoTributariaIS;
