import cx from 'clsx';
import React, { FC, MouseEvent, useEffect, useRef, useState } from 'react';
import { useForm } from 'react-hook-form';
import { RouteComponentProps } from 'react-router';
import { Card, CardBody, CardHeader, Col, CustomInput, Nav, NavItem, NavLink, Row, TabContent, TabPane } from 'reactstrap';
import {
  cepFormatter,
  DefaultSortProps,
  DetailManagerForm,
  DetailManagerFormProps,
  EditForm,
  EditViewProps,
  getSelectedItem,
  InputGroupInline,
  InputType,
  isEmpty,
  onlyAlphaNum,
  onlyNumber,
  OrderType,
  phoneWithDDDFormatter,
  WindowComponent,
  withTooltip,
} from 'summer';
import { LotacaoTributaria } from '../../../../models/dtos/mfp/lotacaoTributaria';
import { CLASS_NAME, TomadorServico } from '../../../../models/dtos/mfp/tomadorServico';
import { TomadorServicoEndereco } from '../../../../models/dtos/mfp/tomadorServicoEndereco';
import { TomadorServicoTelefone } from '../../../../models/dtos/mfp/tomadorServicoTelefone';
import { CODIGO_RECOLHIMENTO_ENUM_VALUES } from '../../../../models/enumerated/mfp/codigoRecolhimentoEnum';
import { INDICATIVO_CONTRATACAO_APRENDIZ_ENUM_VALUES } from '../../../../models/enumerated/mfp/indicativoContratacaoAprendizEnum';
import { INDICATIVO_CONTRATACAO_PCD_ENUM_VALUES } from '../../../../models/enumerated/mfp/indicativoContratacaoPCDEnum';
import { TIPO_CAEPF_ENUM_VALUES } from '../../../../models/enumerated/mfp/tipoCAEPFEnum';
import { TIPO_CONTROLE_PONTO_ENUM_VALUES } from '../../../../models/enumerated/mfp/tipoControlePontoEnum';
import { OBRA_EMPREITADA_PARCIAL_SUBEMPREITADA } from '../../../../models/enumerated/mfp/tipoLotacaoTributariaEnum';
import { tipoTelefoneFormatter } from '../../../../utilities/tipoTelefoneFormatter';
import CentroCustoIS from '../../../components/mcg/centroCustoIS/CentroCustoIS';
import CodigoGpsIS from '../../../components/mfp/codigoGpsIS/CodigoGpsIS';
import FpasIS from '../../../components/mfp/fpasIS/FpasIS';
import LotacaoTributariaIS from '../../../components/mfp/lotacaoTributariaIS/LotacaoTributariaIS';
import TomadorServicoEmailEditView from './TomadorServicoEmailEditView';
import TomadorServicoEnderecoEditView from './TomadorServicoEnderecoEditView';
import TomadorServicoSeletorCnpjCpf from './TomadorServicoSeletorCnpjCpf';
import TomadorServicoTelefoneEditView from './TomadorServicoTelefoneEditView';

interface TomadorServicoEditViewProps extends EditViewProps<TomadorServico> {}

const TomadorServicoEditView: FC<TomadorServicoEditViewProps & RouteComponentProps> = props => {
  const selectedItem = getSelectedItem(props);

  const formProps = useForm<TomadorServico>({
    defaultValues: {
      emails: [],
      enderecos: [],
      isCnpjInscricaoContratante: true,
      isCnpjInscricaoProprietario: true,
      telefones: [],
      ...selectedItem,
    },
    mode: 'onBlur',
  });

  const [activeTab, setActiveTab] = useState(1);
  const [isNotEmpreitada, setIsNotEmpreitada] = useState(true);

  const contratanteRef = useRef();
  const proprietarioRef = useRef();

  const enderecoDefaultSortProps: DefaultSortProps[] = [
    {
      dataField: 'logradouro',
      order: OrderType.ASC,
    },
  ];

  const telefoneDefaultSortProps: DefaultSortProps[] = [
    {
      dataField: 'contato',
      order: OrderType.ASC,
    },
  ];

  const emailDefaultSortProps: DefaultSortProps[] = [
    {
      dataField: 'contato',
      order: OrderType.ASC,
    },
  ];

  const enderecoFormProps: DetailManagerFormProps = {
    columns: [
      {
        dataField: 'logradouro',
        text: 'Logradouro',
      },
      {
        dataField: 'numero',
        text: 'Número',
      },
      {
        dataField: 'cep',
        formatter: cepFormatter,
        text: 'CEP',
      },
      {
        dataField: 'bairro.bairro',
        text: 'Bairro',
      },
      {
        dataField: 'cidade.nome',
        text: 'Cidade',
      },
    ],
    detailView: TomadorServicoEnderecoEditView,
    formProps,
    propName: 'enderecos',
    title: 'Manutenção de Endereços',
  };

  const telefoneFormProps: DetailManagerFormProps = {
    columns: [
      {
        dataField: 'contato',
        text: 'Contato',
      },
      {
        dataField: 'numero',
        formatter: phoneWithDDDFormatter,
        text: 'Telefone',
      },
      {
        dataField: 'tipos',
        formatter: tipoTelefoneFormatter,
        text: 'Tipos',
      },
    ],
    detailView: TomadorServicoTelefoneEditView,
    formProps,
    propName: 'telefones',
    title: 'Manutenção de Telefones',
  };

  const emailFormProps: DetailManagerFormProps = {
    columns: [
      {
        dataField: 'contato',
        text: 'Contato',
      },
      {
        dataField: 'email',
        text: 'Email',
      },
    ],
    detailView: TomadorServicoEmailEditView,
    formProps,
    propName: 'emails',
    title: 'Manutenção de Emails',
  };

  useEffect(
    () => {
      if (selectedItem && selectedItem.id) {
        setTimeout(() => {
          if (
            selectedItem.lotacaoTributaria &&
            selectedItem.lotacaoTributaria.tipoLotacaoTributaria === OBRA_EMPREITADA_PARCIAL_SUBEMPREITADA.value
          ) {
            setIsNotEmpreitada(false);
          }
        }, 500);
      }
    },
    // eslint-disable-next-line react-hooks/exhaustive-deps
    []
  );

  const toggleTab = ({ target }: MouseEvent) => {
    const { tabIndex } = target as HTMLElement;
    if (activeTab !== tabIndex) {
      setActiveTab(tabIndex);
    }
  };

  const tooltip = (message: string, componentId: string) => withTooltip({ message, placement: 'top-start' }, componentId);

  const changeLotacaoTributaria = (lotacaoTributaria: LotacaoTributaria) =>
    setIsNotEmpreitada(!lotacaoTributaria || lotacaoTributaria.tipoLotacaoTributaria !== OBRA_EMPREITADA_PARCIAL_SUBEMPREITADA.value);

  const validateEnderecoPrincipal = (enderecos: TomadorServicoEndereco[]) => enderecos.find(endereco => endereco.tipoEnderecoPrincipal) !== undefined;

  const validateTelefonePrincipal = (telefones: TomadorServicoTelefone[]) => telefones.find(telefone => telefone.tipoTelefonePrincipal) !== undefined;

  const validateExtras = () => {
    // @ts-ignore
    contratanteRef.current.save();

    // @ts-ignore
    proprietarioRef.current.save();

    const tomadorServico = formProps.getValues();

    const erros = [];

    if (!tomadorServico.cei && !tomadorServico.cnpj) {
      erros.push('Campo CNPJ ou CEI/CNO deve ser preenchido.');
    }

    const msgEndereco = 'Pelo menos um endereço deve ser cadastrado';

    // Validação do endereço obrigatório
    if (tomadorServico.enderecos.length === 0) {
      erros.push(msgEndereco + '.');
    } else {
      // Validação do endereço principal obrigatório
      if (!validateEnderecoPrincipal(tomadorServico.enderecos)) {
        erros.push(msgEndereco + ' como principal.');
      }
    }

    // Validação do telefone principal
    if (tomadorServico.telefones.length > 0 && !validateTelefonePrincipal(tomadorServico.telefones)) {
      erros.push('Pelo menos um telefone deve ser cadastrado como principal.');
    }

    if (isNotEmpreitada === false) {
      if (isEmpty(tomadorServico.nrInscricaoContratante)) {
        erros.push('Inscrição do Contratante - Campo não pode estar em branco.');
      }

      if (isEmpty(tomadorServico.nrInscricaoProprietario)) {
        erros.push('Inscrição do Proprietário do CNO - Campo não pode estar em branco.');
      }
    }

    return erros;
  };

  // TODO implementar no afterPersist a função criarEventoPendenteAlteracao do eSocial

  return (
    <WindowComponent title="Manutenção de Tomador de Serviço / Obra de Construção">
      <EditForm formProps={formProps} editViewProps={props} dtoClassName={CLASS_NAME} validateExtras={validateExtras}>
        <input type="hidden" name="nrInscricaoContratante" ref={formProps.register} />
        <input type="hidden" name="nrInscricaoProprietario" ref={formProps.register} />
        <Row>
          <Col md={4}>
            <InputGroupInline
              formProps={{ ...formProps, validation: { ...onlyNumber(0, 8) } }}
              isPrependLabel={true}
              placeholder="Preenchimento Opcional - Codificação Automática"
              id="codigo"
              label="Código"
            />
          </Col>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { required: true, maxLength: 120 } }}
              isPrependLabel={true}
              id="nome"
              label="Nome"
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline formProps={formProps} isPrependLabel={true} id="cnpj" label="CNPJ" type={InputType.CNPJ} />
          </Col>
          <Col>
            <InputGroupInline formProps={{ ...formProps, validation: { ...onlyAlphaNum(0, 12) } }} isPrependLabel={true} id="cei" label="CEI/CNO" />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { min: 0, max: 100 } }}
              isPrependLabel={true}
              id="percentualDesoneracaoGps"
              label="Percentual de Desoneração"
              tooltipProps={{
                message: 'Caso não seja informado o sistema utilizará o percentual cadastrado na configuração da empresa',
              }}
              type={InputType.NUMBER}
            />
          </Col>
          <Col>
            <InputGroupInline formProps={{ ...formProps, validation: { ...onlyAlphaNum(0, 30) } }} isPrependLabel={true} id="caepf" label="CAEPF" />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline formProps={{ ...formProps, validation: { ...onlyNumber(0, 7) } }} isPrependLabel={true} id="cnaef" label="CNAE" />
          </Col>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { ...onlyNumber(0, 2) } }}
              isPrependLabel={true}
              id="cnaefDesmembramento"
              label="Desmembramento CNAE"
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <Nav tabs={true}>
              <NavItem>
                <NavLink className={cx({ active: activeTab === 1 })} tabIndex={1} onClick={toggleTab}>
                  Gerais
                </NavLink>
              </NavItem>
              <NavItem>
                <NavLink className={cx({ active: activeTab === 2 })} tabIndex={2} onClick={toggleTab}>
                  Endereços
                </NavLink>
              </NavItem>
              <NavItem>
                <NavLink className={cx({ active: activeTab === 3 })} tabIndex={3} onClick={toggleTab}>
                  Telefones
                </NavLink>
              </NavItem>
              <NavItem>
                <NavLink className={cx({ active: activeTab === 4 })} tabIndex={4} onClick={toggleTab}>
                  E-Mails
                </NavLink>
              </NavItem>
              <NavItem>
                <NavLink className={cx({ active: activeTab === 5 })} tabIndex={5} onClick={toggleTab}>
                  eSocial
                </NavLink>
              </NavItem>
            </Nav>
            <TabContent className="mb-5" activeTab={activeTab}>
              <TabPane tabId={1} className="mb-0 p-3">
                <Row>
                  <Col>
                    <InputGroupInline
                      formProps={{ ...formProps, validation: { ...onlyNumber(0, 4) } }}
                      isPrependLabel={true}
                      id="codigoDeducaoTerceiros"
                      label="Código Dedução Terceiros"
                    />
                  </Col>
                </Row>
                <Row>
                  <Col>
                    <InputGroupInline
                      formProps={{ ...formProps, validation: { min: 0, max: 100 } }}
                      isPrependLabel={true}
                      id="aliquotaRat"
                      label="Alíquota RAT"
                      tooltipProps={{
                        message:
                          'O campo deve ser menor ou igual a 3,00 exceto em caso processo administrativo/judicial que permite a aplicação de alíquotas diferentes.',
                      }}
                      type={InputType.NUMBER}
                    />
                  </Col>
                  <Col>
                    <InputGroupInline
                      formProps={{ ...formProps, validation: { min: 0, max: 100 } }}
                      isPrependLabel={true}
                      id="aliquotaFap"
                      label="Alíquota FAP"
                      type={InputType.NUMBER}
                    />
                  </Col>
                </Row>
                <Row>
                  <Col>
                    <InputGroupInline
                      id="controlePonto"
                      label="Tipo de Controle de Ponto"
                      formProps={formProps}
                      isPrependLabel={true}
                      placeholder="Informe o Tipo de Controle de Ponto"
                      inputDropDownProps={{ optionsProvider: TIPO_CONTROLE_PONTO_ENUM_VALUES }}
                      type={InputType.DROP_DOWN}
                    />
                  </Col>
                </Row>
                <Row>
                  <Col>
                    <CodigoGpsIS id="codigoGps" formProps={formProps} isPrependLabel={true} label="Código GPS" />
                  </Col>
                </Row>
                <Row>
                  <Col>
                    <CodigoGpsIS id="codigoGpsTerceiros" formProps={formProps} isPrependLabel={true} label="Código GPS (Terceiros)" />
                  </Col>
                </Row>
                <Row>
                  <Col>
                    <FpasIS id="codigoFpas" formProps={formProps} isPrependLabel={true} label="Código FPAS" />
                  </Col>
                </Row>
                <Row>
                  <Col>
                    <InputGroupInline
                      id="codigoRecolhimentoSefip"
                      label="Código SEFIP"
                      formProps={formProps}
                      isPrependLabel={true}
                      placeholder="Informe o Código SEFIP"
                      inputDropDownProps={{ optionsProvider: CODIGO_RECOLHIMENTO_ENUM_VALUES }}
                      type={InputType.DROP_DOWN}
                    />
                  </Col>
                </Row>
                <Row>
                  <Col>
                    <CentroCustoIS id="centroCusto" formProps={formProps} isPrependLabel={true} label="Centro de Custo" />
                  </Col>
                </Row>
              </TabPane>
              <TabPane tabId={2} className="mb-0 p-3">
                <DetailManagerForm
                  {...props}
                  defaultSortDirection={OrderType.ASC}
                  defaultSorted={enderecoDefaultSortProps}
                  {...enderecoFormProps}
                  keyField="rowIndex"
                />
              </TabPane>
              <TabPane tabId={3} className="mb-0 p-3">
                <DetailManagerForm
                  {...props}
                  defaultSortDirection={OrderType.ASC}
                  defaultSorted={telefoneDefaultSortProps}
                  {...telefoneFormProps}
                  keyField="rowIndex"
                />
              </TabPane>
              <TabPane tabId={4} className="mb-0 p-3">
                <DetailManagerForm
                  {...props}
                  defaultSortDirection={OrderType.ASC}
                  defaultSorted={emailDefaultSortProps}
                  {...emailFormProps}
                  keyField="rowIndex"
                />
              </TabPane>
              <TabPane tabId={5} className="mb-0 p-3">
                <Row>
                  <Col>
                    <LotacaoTributariaIS
                      id="lotacaoTributaria"
                      formProps={formProps}
                      isPrependLabel={true}
                      label="Lotação Tributária"
                      onChange={changeLotacaoTributaria}
                    />
                  </Col>
                </Row>
                <Row>
                  <Col>
                    <InputGroupInline
                      id="tipoCaepf"
                      label="Tipo de CAEPF"
                      formProps={formProps}
                      isPrependLabel={true}
                      placeholder="Informe o Tipo de CAEPF"
                      inputDropDownProps={{ optionsProvider: TIPO_CAEPF_ENUM_VALUES }}
                      type={InputType.DROP_DOWN}
                    />
                  </Col>
                </Row>
                <Row>
                  <Col>
                    <InputGroupInline
                      id="indicativoContratacaoAprendiz"
                      label="Indicativo de contratação de aprendiz"
                      formProps={formProps}
                      isPrependLabel={true}
                      placeholder="Informe o Indicativo de contratação de aprendiz"
                      inputDropDownProps={{ optionsProvider: INDICATIVO_CONTRATACAO_APRENDIZ_ENUM_VALUES }}
                      type={InputType.DROP_DOWN}
                    />
                  </Col>
                </Row>
                <Row>
                  <Col>
                    <InputGroupInline
                      id="indicativoContratacaoPCD"
                      label="Indicativo de contratação de PCD"
                      formProps={formProps}
                      isPrependLabel={true}
                      placeholder="Informe o Indicativo de contratação de PCD"
                      inputDropDownProps={{ optionsProvider: INDICATIVO_CONTRATACAO_PCD_ENUM_VALUES }}
                      type={InputType.DROP_DOWN}
                      tooltipProps={{
                        message:
                          'Indicativo de contratação de  pessoa com deficiência (PCD). ' +
                          "Essa informação deve ser prestada apenas no estabelecimento 'Matriz'.",
                      }}
                    />
                  </Col>
                </Row>
                <Row>
                  <Col style={{ margin: '10px 0' }}>
                    <CustomInput
                      type="switch"
                      label="Contribuição Patronal de Obra de Construção Civil Substituída?"
                      name="contribuicaoPatronalSubstituida"
                      id="idcheckContribuicaoPatronalSubstituida"
                      innerRef={formProps.register}
                    />
                    {tooltip('Marque essa opção para indicar ao eSocial que esse tomador deve ser enviado.', 'checkContribuicaoPatronalSubstituida')}
                  </Col>
                </Row>
                <Row>
                  <Col>
                    <Card style={{ marginBottom: '30px', marginTop: '10px' }}>
                      <CardHeader className="font-weight-bold bg-light">Empreitada Parcial</CardHeader>
                      <CardBody>
                        <Row>
                          <Col>
                            <TomadorServicoSeletorCnpjCpf
                              ref={contratanteRef}
                              formProps={formProps}
                              selectedItem={selectedItem}
                              boolField="isCnpjInscricaoContratante"
                              valueField="nrInscricaoContratante"
                              title="Inscrição do Contratante"
                              disabled={isNotEmpreitada}
                            />
                          </Col>
                          <Col>
                            <TomadorServicoSeletorCnpjCpf
                              ref={proprietarioRef}
                              formProps={formProps}
                              selectedItem={selectedItem}
                              boolField="isCnpjInscricaoProprietario"
                              valueField="nrInscricaoProprietario"
                              title="Inscrição do Proprietário do CNO"
                              disabled={isNotEmpreitada}
                            />
                          </Col>
                        </Row>
                      </CardBody>
                    </Card>
                  </Col>
                </Row>
              </TabPane>
            </TabContent>
          </Col>
        </Row>
      </EditForm>
    </WindowComponent>
  );
};

export default TomadorServicoEditView;
