import React, { FC, useEffect } from 'react';
import { useForm } from 'react-hook-form';
import { Card, CardBody, CardHeader, Col, CustomInput, Row } from 'reactstrap';
import { DetailEditForm, DetailEditViewFormProps, InputGroupInline } from 'summer';
import { CepLogradouro } from '../../../../models/dtos/ger/cepLogradouro';
import { TomadorServicoEndereco } from '../../../../models/dtos/mfp/tomadorServicoEndereco';
import CepBairroIS from '../../../components/ger/cepBairroIS/CepBairroIS';
import CepLocalidadeIS from '../../../components/ger/cepLocalidadeIS/CepLocalidadeIS';
import CepLogradouroIS from '../../../components/ger/cepLogradouro/CepLogradouroIS';
import CepTituloIS from '../../../components/ger/cepTituloIS/CepTituloIS';
import CepUFIS from '../../../components/ger/cepUFIS/CepUFIS';

const TomadorServicoEnderecoEditView: FC<DetailEditViewFormProps<TomadorServicoEndereco>> = props => {
  const formProps = useForm<TomadorServicoEndereco>({ defaultValues: props.selectedItem || {}, mode: 'onBlur' });

  // TODO implementar cepManager para preenchimento automático dos campos de endereço

  const cepChange = (result: CepLogradouro) => {
    const data: CepLogradouro = {
      bairro: null,
      cep: null,
      localidade: null,
      nome: null,
      titulo: null,
      uf: null,
      ...result,
    };

    formProps.setValue('cep', data.cep);
    formProps.setValue('titulo', data.titulo);
    formProps.setValue('logradouro', data.nome);
    formProps.setValue('uf', data.uf);
    formProps.setValue('cidade', data.localidade);
    formProps.setValue('bairro', data.bairro);
  };

  useEffect(() => {
    if (props.selectedItem && props.selectedItem.id) {
      setTimeout(() => {
        // @ts-ignore
        formProps.setValue('cepFmt', { cep: props.selectedItem.cep, label: props.selectedItem.cep });
      }, 500);
    }
  });

  return (
    <DetailEditForm handleSubmit={props.handleSubmit} handleCancel={props.handleCancel} formProps={formProps}>
      <Row>
        <Col>
          <CepLogradouroIS
            formProps={{ ...formProps, validation: { required: true } }}
            id="cepFmt"
            isPrependLabel={true}
            label="CEP"
            placeholder="Informe o CEP"
            onChange={cepChange}
          />
          <input type="hidden" name="cep" ref={formProps.register} />
        </Col>
      </Row>
      <Row>
        <Col>
          <CepTituloIS id="titulo" isPrependLabel={true} formProps={{ ...formProps, validation: { required: true } }} />
        </Col>
      </Row>
      <Row>
        <Col>
          <InputGroupInline
            formProps={{ ...formProps, validation: { required: true, maxLength: 150 } }}
            id="logradouro"
            isPrependLabel={true}
            label="Logradouro"
            placeholder="Informe o logradouro"
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <InputGroupInline
            formProps={{ ...formProps, validation: { required: true, maxLength: 10 } }}
            id="numero"
            isPrependLabel={true}
            label="Numero"
            placeholder="Informe o número"
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <InputGroupInline
            formProps={{ ...formProps, validation: { maxLength: 60 } }}
            id="complemento"
            isPrependLabel={true}
            label="Complemento"
            placeholder="Informe o complemento"
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <CepUFIS formProps={{ ...formProps, validation: { required: true } }} id="uf" />
        </Col>
      </Row>
      <Row>
        <Col>
          <CepLocalidadeIS formProps={{ ...formProps, validation: { required: true } }} id="cidade" />
        </Col>
      </Row>
      <Row>
        <Col>
          <CepBairroIS formProps={{ ...formProps, validation: { required: true } }} id="bairro" />
        </Col>
      </Row>
      <Row>
        <Col style={{ marginBottom: '30px' }}>
          <Card>
            <CardHeader className="font-weight-bold bg-light">Tipo de Endereço</CardHeader>
            <CardBody>
              <Row>
                <Col style={{ marginBottom: '10px' }}>
                  <CustomInput
                    type="switch"
                    label="Principal"
                    name="tipoEnderecoPrincipal"
                    id="checkTipoEnderecoPrincipal"
                    innerRef={formProps.register}
                  />
                </Col>
              </Row>
              <Row>
                <Col style={{ marginBottom: '10px' }}>
                  <CustomInput
                    type="switch"
                    label="Residencial"
                    name="tipoEnderecoResidencial"
                    id="checkTipoEnderecoResidencial"
                    innerRef={formProps.register}
                  />
                </Col>
              </Row>
              <Row>
                <Col style={{ marginBottom: '10px' }}>
                  <CustomInput
                    type="switch"
                    label="Cobrança"
                    name="tipoEnderecoCobranca"
                    id="checkTipoEnderecoCobranca"
                    innerRef={formProps.register}
                  />
                </Col>
              </Row>
              <Row>
                <Col>
                  <CustomInput
                    type="switch"
                    label="Comercial"
                    name="tipoEnderecoComercial"
                    id="checkTipoEnderecoComercial"
                    innerRef={formProps.register}
                  />
                </Col>
              </Row>
            </CardBody>
          </Card>
        </Col>
      </Row>
    </DetailEditForm>
  );
};

export default TomadorServicoEnderecoEditView;
