import React, { FC } from 'react';
import { useForm } from 'react-hook-form';
import { Col, Row } from 'reactstrap';
import { DetailEditForm, DetailEditViewFormProps, InputGroupInline, InputType } from 'summer';
import { TomadorServicoEmail } from '../../../../models/dtos/mfp/tomadorServicoEmail';

const TomadorServicoEmailEditView: FC<DetailEditViewFormProps<TomadorServicoEmail>> = props => {
  const formProps = useForm<TomadorServicoEmail>({ defaultValues: props.selectedItem || {}, mode: 'onBlur' });

  return (
    <DetailEditForm handleSubmit={props.handleSubmit} handleCancel={props.handleCancel} formProps={formProps}>
      <Row>
        <Col>
          <InputGroupInline
            formProps={{ ...formProps, validation: { maxLength: 120 } }}
            id="contato"
            isPrependLabel={true}
            label="Contato"
            placeholder="Informe o contato"
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <InputGroupInline
            formProps={{ ...formProps, validation: { maxLength: 120, required: true } }}
            id="email"
            isPrependLabel={true}
            label="Email"
            placeholder="Informe o email"
            type={InputType.EMAIL}
          />
        </Col>
      </Row>
    </DetailEditForm>
  );
};

export default TomadorServicoEmailEditView;
