import cx from 'clsx';
import React, { forwardRef, useEffect, useImperativeHandle, useState } from 'react';
import { useForm } from 'react-hook-form';
import { Card, CardBody, CardHeader, CustomInput } from 'reactstrap';
import { InputGroupInline, InputType, UseFormProps } from 'summer';

export interface TomadorServicoSeletorCnpjCpfProps {
  formProps?: UseFormProps;
  selectedItem: any;
  title?: string;
  boolField: string;
  valueField: string;
  disabled?: boolean;
}

interface TomadorServicoSeletorCnpjCpfDto {
  valorCpf?: string;
  valorCnpj?: string;
}

const TomadorServicoSeletorCnpjCpf = forwardRef<any, TomadorServicoSeletorCnpjCpfProps>((props, ref) => {
  const internalFormProps = useForm<TomadorServicoSeletorCnpjCpfDto>({ mode: 'onBlur' });

  const [inputType, setInputType] = useState<InputType>(props.selectedItem[props.boolField] ? InputType.CNPJ : InputType.CPF);

  const changeInputType = (fieldInputType: InputType) => {
    return () => {
      setInputType(fieldInputType);
      internalFormProps.setValue('valorCnpj', null);
      internalFormProps.setValue('valorCpf', null);
    };
  };

  useEffect(
    () => {
      if (props.selectedItem && props.selectedItem.id) {
        setTimeout(() => {
          props.formProps.register({ name: props.boolField });

          const isCnpj = props.selectedItem[props.boolField];
          const value = props.selectedItem[props.valueField];

          internalFormProps.reset({
            valorCnpj: isCnpj ? value : null,
            valorCpf: isCnpj ? null : value,
            [props.boolField]: isCnpj ? 'true' : 'false',
          });
        }, 500);
      }
    },
    // eslint-disable-next-line react-hooks/exhaustive-deps
    []
  );

  useImperativeHandle(ref, () => ({
    save() {
      const internalFormData = internalFormProps.getValues();
      const isCnpj = internalFormData[props.boolField] === 'true';

      props.formProps.setValue(props.boolField, isCnpj);
      props.formProps.setValue(props.valueField, (isCnpj ? internalFormData.valorCnpj : internalFormData.valorCpf) || null);
    },
  }));

  return (
    <Card>
      <CardHeader className="font-weight-bold bg-light">{props.title}</CardHeader>
      <CardBody className={cx({ 'bg-light': props.disabled || false })}>
        <div className="d-flex">
          <div style={{ width: 185, paddingTop: 10 }}>
            <CustomInput
              id={props.boolField + '-True'}
              inline={true}
              innerRef={internalFormProps.register}
              label="CNPJ"
              name={props.boolField}
              value="true"
              type="radio"
              disabled={props.disabled || false}
              onChange={changeInputType(InputType.CNPJ)}
            />
            <CustomInput
              id={props.boolField + '-False'}
              inline={true}
              innerRef={internalFormProps.register}
              label="CPF"
              name={props.boolField}
              value="false"
              type="radio"
              disabled={props.disabled || false}
              onChange={changeInputType(InputType.CPF)}
            />
          </div>
          <div className="flex-grow-1">
            <InputGroupInline
              id="valorCnpj"
              formProps={internalFormProps}
              type={InputType.CNPJ}
              hidden={inputType !== InputType.CNPJ}
              isPrependLabel={true}
              label="CNPJ"
              disabled={props.disabled || false}
            />
            <InputGroupInline
              id="valorCpf"
              formProps={internalFormProps}
              type={InputType.CPF}
              hidden={inputType !== InputType.CPF}
              isPrependLabel={true}
              label="CPF"
              disabled={props.disabled || false}
            />
          </div>
        </div>
      </CardBody>
    </Card>
  );
});

export default TomadorServicoSeletorCnpjCpf;
